import React, { useState } from 'react';
import Swiper from 'react-id-swiper';
import Portfolio from './Portfolio';
import SliderPrevButton from './SliderPrevButton';
import SliderNextButton from './SliderNextButton';
import SliderThumbs from './SliderThumbs';
import { sliderOpts, checkSlideColour, changeCaption, changeCollection } from '../utils/slider'; 

function Slider({ data, updateTheme, updateThumbsActive }) {
  const [collection, updateCollection] = useState("");
  const [caption, updateCaption] = useState("");
  const [swiper, updateSwiper] = useState(null);
  const portfolioList = data.find(doc => doc.type === "portfolio_list");
  const portfolios = data.filter(doc => doc.type === "portfolio");
  const params = {
    ...sliderOpts,
    renderPrevButton: () => <SliderPrevButton />,
    renderNextButton: () => <SliderNextButton />,
    on: {
      slideChange: function() {
        updateTheme(checkSlideColour(this));
        updateCaption(changeCaption(this));
        updateCollection(changeCollection(this));
      }
    }
  }
  const goToSlide = (slideIdx) => {
    if (swiper !== null) {
      swiper.slideToLoop(slideIdx)
    }
    setTimeout(() => updateThumbsActive(false), 200)
  }

  return (
    <>
      <p className="collection muted small">{collection}</p>
      {!!portfolioList &&
        <Swiper getSwiper={updateSwiper} {...params}>
          {portfolioList.data.portfolios.map(p =>
            <Portfolio 
              key={p.portfolio.id} 
              id={p.portfolio.id} 
              data={portfolios}
            />
          )}
        </Swiper>
      }
      <SliderThumbs
        portfolioList={portfolioList}
        portfolios={portfolios}
        goToSlide={goToSlide}
      />
      <p className="caption muted small">{caption}</p>
    </>
  )
}

export default Slider;