import React from 'react';

function generateThumbGroup(portfolioList, portfolios, goToSlide) {
  //let slideIdx = -1;
  let slideIdx = 0;
  return portfolioList.data.portfolios.map((p, idx) => {
    const portfolio = portfolios.find(port => port.id === p.portfolio.id)
    const slideIdxNumForCover = slideIdx;

    return (
      <div key={idx} className="thumb-group">
        <div className="thumb-group-heading">
          <p
            className="slide-link muted"
            onClick={() => goToSlide(slideIdxNumForCover)}
          >
            {portfolio.data.name}
          </p>
        </div>
        <div className="thumb-group-items">
          {portfolio.data.body.map((slide, subIdx) => {
            // slideIdx++;
            // const slideIdxNumForSlide = slideIdx;
            const slideIdxNumForSlide = slideIdx;
            slideIdx++;

            return slide.slice_type !== "text" 
            ? (
              <div
                key={subIdx}
                className="slide-link"
                onClick={() => goToSlide(slideIdxNumForSlide)}
              >
                <div
                  className="thumb-group-item-image"
                  style={{backgroundImage: `url(${!!slide.primary.image_one.square && slide.primary.image_one.square.url})`}}
                />
                {/* <p>{slideIdxNumForSlide}</p> */}
              </div>
            ) : false
          })}
        </div>
      </div>  
    )
  })
}

function SliderThumbs({portfolioList, portfolios, goToSlide }) {
  return (
    <div className="thumbs-container">
			<div className="thumbs-inner">
        {generateThumbGroup(portfolioList, portfolios, goToSlide)}
      </div>
    </div>
  )
}

export default SliderThumbs