import React from 'react';

function Header({ 
	aboutActive, 
	updateAboutActive, 
	thumbsActive, 
	updateThumbsActive 
}) {
  return (
    <header>
			<div>
				<a href="/" title="Scott Lyttle">Scott Lyttle</a>
			</div>
			<div>
				{!aboutActive &&
					<span
						className="link about-toggle"
						onClick={() => updateAboutActive(true)}
					>
						About
					</span>
				}
				{(aboutActive || thumbsActive) &&
					<div
						className="overlay-toggle link"
						onClick={() => {
							updateAboutActive(false)
							updateThumbsActive(false)
						}}
					>
						<svg width="26" height="26" viewBox="0 0 26 26" fill="none" xmlns="http://www.w3.org/2000/svg">
							<path d="M25 1L1 25" stroke="black" strokeWidth="1.5" strokeLinejoin="round"/>
							<path d="M1 1L25 25" stroke="black" strokeWidth="1.5" strokeLinejoin="round"/>
						</svg>
					</div>
				}
			</div>
		</header>
  )
}

export default Header