import React, { useEffect, useState } from 'react';
import { Client } from './utils/prismic';
import Header from './components/Header';
import Footer from './components/Footer';
import Slider from './components/Slider';
import About from './components/About';
import MobileSwipePrompt from './components/MobileSwipePrompt';
import './style/app.scss';

function App() {
  const [doc, setDocData] = useState([]);
  const [theme, updateTheme] = useState("dark");
  const [aboutActive, updateAboutActive] = useState(false);
  const [thumbsActive, updateThumbsActive] = useState(false);

	useEffect(() => {
    const fetchData = async () => {
      const response = await Client.query('', { pageSize: 200 })
      if (response) {
        setDocData(response.results)
      } else {
        console.log('no bueno')
      }
    }
    fetchData()
  }, [])

  return (
    <div className={`page_wrap ${theme} ${aboutActive ? 'about-active' : ''} ${thumbsActive ? 'thumbs-active' : ''}`}>
      <Header
        aboutActive={aboutActive}
        updateAboutActive={updateAboutActive}
        thumbsActive={thumbsActive}
        updateThumbsActive={updateThumbsActive}
      />
      {!!doc.length
        ? <>
            <Slider
              data={doc}
              updateTheme={updateTheme}
              updateThumbsActive={updateThumbsActive} 
            />
            <About
              class={aboutActive && 'active'}
              data={doc}
            />
          </> 
        : <div className="swiper-lazy-preloader"></div>
      }
      <Footer
        aboutActive={aboutActive}
        updateAboutActive={updateAboutActive}
        thumbsActive={thumbsActive}
        updateThumbsActive={updateThumbsActive}
      />
      <MobileSwipePrompt />
    </div>
  );
}

export default App;

