export const sliderOpts = {
  loop: true,
  effect: window.innerWidth > 500 ? 'fade' : 'slide',
  simulateTouch: window.innerWidth > 500 ? false : true,
  keyboardControl: true,
  mousewheelControl: false,
  spaceBetween: 0,
  slidesPerView: 1,
  preloadImages: false,
  lazy: true,
  loadPrevNext: true, 
  loadPrevNextAmount: 4,
  //loadOnTransitionStart: true,
  navigation: {
    nextEl: '.swiper-button-next',
    prevEl: '.swiper-button-prev',
  },
  hashNavigation: {
    replaceState: true,
  }
}
export function checkSlideColour(swiperObj) {
  const { slides, activeIndex } = swiperObj;
  return !!slides[activeIndex].dataset.color
    ? slides[activeIndex].dataset.color
    : "dark"
}

export function changeCaption(swiperObj) {
  const { slides, activeIndex } = swiperObj;

  if (slides[activeIndex].dataset.caption) {
    return slides[activeIndex].dataset.caption
  } else {
    return '';
  }
}

export function changeCollection(swiperObj) {
  const { slides, activeIndex } = swiperObj;

  if (slides[activeIndex].dataset.collection) {
    return slides[activeIndex].dataset.collection
  } else {
    return '';
  }
}

