import React from 'react';

function hideAboutShowThumbs(updateAboutActive, updateThumbsActive) {
	updateThumbsActive(true);
	updateAboutActive(false);
}

function Footer({ aboutActive, updateAboutActive, thumbsActive, updateThumbsActive }) {
  return (
    <footer>
			<a 
				className="insta-toggle"
				href="https://instagram.com/scottlyttle" 
				target="_blank" 
				rel="noopener noreferrer" 
				title="Instagram"
			>
				<svg width="24" height="23" viewBox="0 0 24 23" fill="none" xmlns="http://www.w3.org/2000/svg">
					<rect x="0.75" y="0.75" width="22.5" height="21.5" stroke="black" strokeWidth="1.5"/>
					<circle cx="12" cy="12" r="5.25" stroke="black" strokeWidth="1.5"/>
					<circle cx="19.5" cy="4.5" r="0.75" stroke="black" strokeWidth="1.5"/>
				</svg>
			</a>
			<div
				className="link thumbs-toggle"
				onClick={() => thumbsActive 
					? updateThumbsActive(false) 
					: aboutActive
					? hideAboutShowThumbs(updateAboutActive, updateThumbsActive)
					: updateThumbsActive(true)}
			>
				<svg width="24" height="23" viewBox="0 0 24 23" fill="none" xmlns="http://www.w3.org/2000/svg">
					<rect x="0.75" y="0.75" width="22.5" height="21.5" stroke="black" strokeWidth="1.5"/>
					<line x1="8.75" y1="1" x2="8.75" y2="22" stroke="black" strokeWidth="1.5"/>
					<line x1="15.75" y1="1" x2="15.75" y2="22" stroke="black" strokeWidth="1.5"/>
					<line y1="8.25" x2="23" y2="8.25" stroke="black" strokeWidth="1.5"/>
					<line y1="15.25" x2="23" y2="15.25" stroke="black" strokeWidth="1.5"/>
				</svg>
			</div>
		</footer>
  )
}

export default Footer