import React from 'react';
//import PortfolioCover from './PortfolioCover';
import PortfolioSlide from './PortfolioSlide';

function Portfolio({ 
    id, 
    data
  }) {
  const portfolio = data.find(doc => doc.id === id);
  const slug = portfolio.uid
  
  return (
    <>
      {/* <PortfolioCover
        image={portfolio.data.cover_image}
        name={portfolio.data.name}
        slug={slug}
        preheading={portfolio.data.preheading}
      /> */}
      {portfolio.data.body.map((s, idx) => 
        <PortfolioSlide
          key={idx}
          idx={idx}
          slug={slug}
          name={portfolio.data.name}
          slideData={s}
        />
      )}
    </>
  )
}

export default Portfolio;