import React from 'react';
import { RichText } from 'prismic-reactjs';

function PortfolioSlide({slideData, slug, name, idx}) {
  const { primary, slice_type } = slideData;
  const { image_one, text } = primary;

  return (
    <div
      data-hash={`${slug}-${idx+1}`}
      className={`swiper-slide ${slice_type === "text" && "slide-has-text"}`}
      data-caption={!!image_one ? image_one.alt : ''}
      data-collection={name}
    >
      <div>
        {slice_type === "text" ? (
          <div className="slide-text">
            <RichText render={text} />
          </div>
        ) : (
          <>
            <img alt="" data-src={`${image_one.url}&fit=clip&w=1650&h=1100`} className="swiper-lazy" />
            <div className="swiper-lazy-preloader"></div>
          </>
        )}
      </div>
    </div>
  )
}

export default PortfolioSlide